import styled from "@emotion/styled"
import { Button, Typography } from "@mui/material"
import { colors } from "../../constants/colors"

export const PageContainer = styled.div(() => ({
  display: 'flex',
  flexDirection: 'column',
  gap: '35px',
  width: '820px',
  margin: '0 auto'
}))
  
export const BattleSection = styled.section(() => ({
  display: 'flex',
  alignItems: 'center',
  gap: '25px'
}))
  
  
  
export const CreateMonsterButton = styled(Button, )(({ disabled }) => ({
  background: disabled ? colors.lightGreen : colors.darkGreen,
  boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  borderRadius: '5px',
  fontFamily: 'Roboto',
  fontStyle: 'normal',
  fontWeight: '400',
  fontSize: '18px',
  lineHeight: '21px',
  color: `${colors.white} !important`,
  padding: '12px 30px',
  margin: '0 auto',
  textTransform: 'capitalize',
  '&:hover': {
    background: colors.darkGreenHover
  }
}))

export const MonstersTitle = styled(Typography)(() => ({
  fontFamily: 'Roboto',
  fontStyle: 'normal',
  fontWeight: '400',
  fontSize: '28px',
  lineHeight: '28px',
  color: colors.black,
  margin: '0 auto 40px'
}))
